<script>
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import Stats from "stats.js";

export default {
  name: "HelloWorld",
  data() {
    return {
      container: null,
      scene: null,
      camera: null,
      controls: null,
      renderer: null,
      stats: null,
      translatePx: 0,
      renderStarted: false,
    };
  },
  methods: {
    init() {
      // set container
      this.container = this.$refs.sceneContainer;

      // add stats
      this.stats = new Stats();
      // this.container.appendChild(this.stats.dom);
      // add camera
      const fov = 60; // Field of view
      const aspect = this.container.clientWidth / this.container.clientHeight;
      const near = 0.5; // the near clipping plane
      const far = 10; // the far clipping plane
      const camera = new THREE.PerspectiveCamera(fov, aspect, near, far);
      camera.position.set(0, 0, 7);
      this.camera = camera;

      // create scene
      this.scene = new THREE.Scene();
      //      this.scene.background = new THREE.Color("transparent");

      // add lights
      const ambientLight = new THREE.HemisphereLight(
        0xffffff, // bright sky color
        0x222222, // dim ground color
        1 // intensity
      );
      const mainLight = new THREE.DirectionalLight(0xffffff, 4.0);
      mainLight.position.set(10, 10, 10);
      this.scene.add(ambientLight, mainLight);

      // add controls
      this.controls = new OrbitControls(this.camera, this.container);

      // create renderer
      this.renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
      this.renderer.setSize(
        this.container.clientWidth,
        this.container.clientHeight
      );
      this.renderer.setPixelRatio(window.devicePixelRatio);
      this.renderer.gammaFactor = 2.2;
      this.renderer.outputEncoding = THREE.sRGBEncoding;
      this.renderer.physicallyCorrectLights = true;
      this.container.appendChild(this.renderer.domElement);
      this.renderer.setClearColor(0x000000, 0); // the default

      // set aspect ratio to match the new browser window aspect ratio
      this.camera.aspect =
        this.container.clientWidth / this.container.clientHeight;
      this.camera.updateProjectionMatrix();
      this.renderer.setSize(
        this.container.clientWidth,
        this.container.clientHeight
      );

      const loader = new GLTFLoader();

      loader.load(
        "/three-assets/bodyShape.glb",
        (gltf) => {
          this.scene.add(gltf.scene);
        },
        undefined,
        undefined
      );

      this.renderer.setAnimationLoop(() => {
        this.render();
      });
    },
    render() {
      this.renderer.render(this.scene, this.camera);
      this.renderStarted = true;
      //  this.stats.update();
    },
    handleScroll() {
      const container = document.getElementById("scene-container");
      if (!this.renderStarted) return;
      // scroll 0.1
      let evalTranscation = "";
      let scroll = window.scrollY / 45;
      const x = Math.floor(scroll / 4);
      console.log(scroll);
      if (scroll < 4 && x % 2 === 0) {
        evalTranscation = `0 + ${scroll}`; // 0 + 0.1
      } else if (scroll >= 4 * x && x % 2 === 1) {
        evalTranscation = `${4 * x} +  (${4} - ${scroll})`; // 4  4 - 4.1
      } else if (scroll >= 4 * x && x % 2 === 0) {
        evalTranscation = `0 - ( 4 * ${x} - ${scroll})`; // 0 + 0.1
      }
      console.log("eval is: ", evalTranscation, eval(evalTranscation));
      this.camera.position.x = eval(evalTranscation);
      container.style.top = 0 - 300 + window.scrollY / 0.55 + "px";

      this.renderer.render(this.scene, this.camera);
    },
  },
  mounted() {
    this.init();
    window.addEventListener("scroll", this.handleScroll);
  },
};
</script>

